<template lang="pug">
  .container
    //- h1.films-title
    //-   | LISTADO DE PELÍCULAS
    .spinner(v-show="isLoading")
      i.fas.fa-spinner.fa-spin
    .films-container.films-container--flex
      div(v-for="film in films" :key="film.id")
        Film(:film="film")
</template>

<script>
import api from "@/api";
import Film from "@/components/Film";

export default {
  components: {
    Film
  },
  data() {
    return {
      films: [],
      isLoading: true
    };
  },
  created() {
    api.getFilms().then(response => {
      this.films = response.data;
      this.isLoading = false;
    });
  }
};
</script>

<style scoped lang="scss">
  .container {
    overflow: hidden;
    margin: 0 auto;
    width: 80%;
  }

  .films-title {
    text-align: center;
  }

  .films-container {
    &--flex {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-content: flex-start;
      align-items: center;
    }
  }
</style>
